import styled from "styled-components";

import { colors, spacingMap } from "../styleguide";

import { FlowCardIconProps, IconVariant, StyledFlowCardProps } from "./FlowCard.types";

export const iconBackgroundColors: Record<IconVariant, string> = {
	primary: colors.darkBlue,
	success: colors.teal,
	default: colors.gold,
};

export const StyledFlowCard = styled.div<StyledFlowCardProps>`
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	width: ${({ maxCardWidth }) => maxCardWidth || "225px"};
	height: 100%;
	background: ${colors.white};
	box-shadow: 0 0 1px ${colors.black}52;
	border-radius: 16px;
	padding: ${spacingMap.xs};
	overflow: hidden;

	border: ${({ isInvalid }) => isInvalid ? `1px ${colors.destructive} solid` : "none"};

	&:hover {
		outline: ${({ isInvalid }) => !isInvalid && `1px ${colors.black} solid`};
		cursor: pointer;
	}

	&:active {
		box-shadow: 0 2px 16px -2px ${colors.midBlue}40;
		outline: ${({ isInvalid }) => !isInvalid && `2px ${colors.midBlue} solid`};
	}
`;

export const FlowCardHeaderWrapper = styled.div`
	display: flex;
	align-items: center;
	width: 100%;
`;

export const FlowCardIcon = styled.div<FlowCardIconProps>`
	border-radius: 8px;
	padding: ${spacingMap.xs};
	height: 16px;
	width: 16px;
	background-color: ${({ iconVariant }) => iconVariant && iconBackgroundColors[iconVariant]}
`;
